@font-face {
  font-family: "TT-Firs";
  src: url("../fonts1/TTFirsNeue-Medium.woff");
  src: local("TT Med"), local("TT-Med"), url("../fonts1/TTFirsNeue-Medium.woff") format("woff"), url("../fonts1/TTFirsNeue-Medium.ttf") format("truetype");
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "TT-Firs";
  src: url("../fonts1/TTFirsNeue-Regular.woff");
  src: local("TT Med"), local("TT-Med"), url("../fonts1/TTFirsNeue-Regular.woff") format("woff"), url("../fonts1/TTFirsNeue-Regular.ttf") format("truetype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "TT-Firs";
  src: url("../fonts1/TTFirsNeue-DemiBold.woff");
  src: local("TT Med"), local("TT-Med"), url("../fonts1/TTFirsNeue-DemiBold.woff") format("woff"), url("../fonts1/TTFirsNeue-DemiBold.ttf") format("truetype");
  font-weight: 600;
  font-style: normal;
}
:root {
  --ff: "TT-Firs";
  --w-clr: 255, 255, 255;
  --w-clr_300: 144, 148, 165;
  --b-clr: 0, 0, 0;
  --ppl-clr: 139, 29, 255;
  --bl-clr: 0, 174, 255;
  --body-clr: 12, 11, 10;
  --y-clr: 255, 208, 65;
  --dark-clr: 15, 15, 27;
  --header-clr: 20, 23, 36;
  --main-text-clr: 96, 92, 83;
  --o-clr: #E7A82F;
  --main-clr: #E4BC6D;
  --main-gradient: (180deg, rgba(228, 188, 109, 0.00) 0%, #E4BC6D 100%);
  --text-xxs: 0.625rem;
  --text-xs: 0.75rem;
  --text-sm: 0.875rem;
  --text-base: 1rem;
  --text-xm: 1.125rem;
  --text-lg: 1.25rem;
  --text-xl: 1.5rem;
  --text-2xl: 1.75rem;
  --text-3xl: 2rem;
  --text-4xl: 2.25rem;
  --text-5xl: 2.5rem;
  --cont-px: 1rem;
  --cont-w: 77.5rem;
}

.heading {
  --heading-max-w: 100%;
  --heading-mb: clamp(3.5rem, 5vw, 5rem);
  --text-clr: #fdfdfd;
  --ls: -0.54px;
  max-width: var(--heading-max-w);
  width: 100%;
  color: var(--text-clr);
  font-size: clamp(1.75rem, 3.375rem, 3.375vw);
  font-weight: 500;
  line-height: 110%;
  letter-spacing: var(--ls);
  text-align: center;
  margin-bottom: var(--heading-mb);
  margin-left: auto;
  margin-right: auto;
}
.heading > span {
  color: var(--main-clr);
}
.heading--dark {
  --text-clr: #0c0b0a;
}
.heading--smaller-mb {
  --heading-mb: clamp(3.5rem, 3.75rem, 3.75rem);
}

body {
  margin: 0;
  padding: 0;
  color: rgba(var(--w-clr), 1);
  background-color: rgba(var(--body-clr), 1);
  width: 100%;
  position: relative;
  font-family: inherit;
  font-weight: inherit;
  font-size: inherit;
  line-height: inherit;
  min-height: -webkit-fill-available;
  display: flex;
  flex-direction: column;
  scroll-behavior: smooth;
  overflow-x: hidden;
  min-height: 100vh;
  position: relative;
}

html {
  box-sizing: border-box;
  width: 100%;
  font-family: var(--ff);
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  min-height: 100vh;
  overflow-x: hidden;
  min-height: -webkit-fill-available;
  scroll-behavior: smooth;
}

.locked {
  overflow: hidden;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

a {
  display: inline-flex;
  color: inherit;
  font-weight: inherit;
  font-style: inherit;
  font-family: inherit;
  text-decoration: none;
  cursor: pointer;
  margin: 0;
}

button {
  background-color: transparent;
  border: none;
  display: inline-flex;
  outline: none;
  cursor: pointer;
  padding: 0;
  margin: 0;
  line-height: initial;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

picture {
  display: inline-flex;
}

input {
  width: 100%;
  outline: none;
  border: none;
  border-radius: 0;
}

label {
  display: block;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

blockquote {
  margin: 0;
}

video::-webkit-media-controls {
  display: none;
}

/* Could Use thise as well for Individual Controls */
video::-webkit-media-controls-play-button {
  display: none;
}

video::-webkit-media-controls-volume-slider {
  display: none;
}

video::-webkit-media-controls-mute-button {
  display: none;
}

video::-webkit-media-controls-timeline {
  display: none;
}

video::-webkit-media-controls-current-time-display {
  display: none;
}

textarea {
  resize: none;
}

dialog {
  margin: 0;
  padding: 0;
  background-color: transparent;
  max-width: 100%;
  max-height: 100%;
  border: none;
  outline: none;
}

figure {
  margin: 0;
}

.container {
  padding: 0 var(--cont-px);
  width: 100%;
  max-width: calc(var(--cont-w) + var(--cont-px) * 2);
  margin-inline: auto;
}

.header {
  padding: clamp(2.5rem, 3.75vw, 3.75rem) 0 0 0;
}
@media only screen and (max-width: 40em) {
  .header {
    padding-top: 3.75rem;
  }
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header__logo {
  max-width: 11.25rem;
  margin-left: auto;
  margin-right: auto;
  transition: 300ms ease;
}
@media only screen and (max-width: 40em) {
  .header__logo {
    max-width: 8.75rem;
  }
}
@media (hover: hover) and (pointer: fine) {
  .header__logo:hover {
    filter: brightness(120%) drop-shadow(0 0 1rem rgba(var(--w-clr), 0.25));
    transform: scale(1.025);
  }
}

.quotes {
  padding: clamp(4rem, 5vw, 5rem) 0 2.125rem 0;
  position: relative;
}
.quotes__hidden {
  --item-bg: 25, 23, 20;
  display: none;
}
.quotes__heading {
  margin-bottom: clamp(3rem, 5vw, 5rem) !important;
}
.quotes__slider-pag {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 6px;
  margin-top: 1.25rem;
}
.quotes__slider-pag span {
  background-color: #E4BC6D !important;
  opacity: 0.3 !important;
  width: 0.75rem;
  height: 0.375rem;
  display: block;
  border-radius: 1.25rem;
  transition: 300ms ease;
}
.quotes__slider-pag .swiper-pagination-bullet-active {
  width: 28px !important;
  opacity: 1 !important;
}
.quotes:before {
  position: absolute;
  content: "";
  width: 2000px;
  height: 770px;
  background-image: url("../img1/quotes/quotes-section-overlay.svg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  pointer-events: none;
  top: 308px;
  z-index: -1;
  left: 50%;
  transform: translateX(-50%);
}
.quotes__heading {
  --heading-max-w: 55.125rem;
  margin-bottom: 7.5rem;
}
.quotes__grid {
  --item-bg: 25, 23, 20;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(25.125rem, 1fr));
  grid-gap: 1rem;
}
.quotes__grid-item {
  border-radius: clamp(1.75rem, 2.5vw, 2.5rem);
  backdrop-filter: blur(7px);
  background-color: rgba(var(--item-bg), 0.6);
  border: 1px solid rgba(232, 206, 156, 0.25);
  padding: 2.125rem 2.125rem 2.625rem 2.125rem;
  position: relative;
  min-height: 25.125rem;
  overflow: hidden;
  position: relative;
  transition: 200ms ease;
  display: flex;
  flex-direction: column;
  grid-gap: 0;
  align-items: initial;
  justify-content: space-between;
}
.quotes__grid-item::before {
  position: absolute;
  content: "";
  width: 80%;
  height: 80%;
  background-image: url("../img1/quotes/item-shadow.svg");
  background-size: 100% 100%;
  background-repeat: no-repeat;
  bottom: 0;
  right: 0;
  pointer-events: none;
}
@media only screen and (max-width: 40em) {
  .quotes__grid-item {
    padding: 1.25rem;
  }
}
@media (hover: hover) and (pointer: fine) {
  .quotes__grid-item:hover {
    border: 1px solid rgba(232, 206, 156, 0.5);
  }
  .quotes__grid-item:hover .quotes__author-image {
    opacity: 0.8;
  }
}
.quotes__grid-item:nth-child(1) .quotes__author-image {
  bottom: -5rem;
}
.quotes__grid-item:nth-child(2) {
  transform: translateY(-40px);
}
.quotes__grid-item:nth-child(2) .quotes__author-image {
  bottom: -5rem;
  right: -1rem;
}
.quotes__grid-item:last-child .quotes__author-image {
  right: -3.75rem;
  bottom: -4.5rem;
}
.quotes__grid-info {
  --text-clr: #efefef;
}
.quotes__grid-info p {
  color: var(--text-clr);
  font-size: clamp(1.125rem, 1.375vw, 1.375rem);
  font-weight: 400;
  line-height: 128%;
}
.quotes__grid-author {
  font-size: var(--text-lg);
  font-weight: 500;
  line-height: 128%;
  color: var(--main-clr);
  position: relative;
  z-index: 1;
}
.quotes__grid-icon {
  width: 2rem;
  height: 2rem;
}
.quotes__grid-icon img {
  display: block;
  width: 100%;
  height: 100%;
}
.quotes__grid-content {
  position: relative;
  z-index: 1;
}
.quotes__author-image {
  position: absolute;
  bottom: -3.5rem;
  right: -0.75rem;
  max-width: 17.125rem;
  opacity: 0.5;
  transition: 300ms ease;
}

@media only screen and (max-width: 78.5em) {
  .quotes::before {
    top: 150px;
    width: 87.5rem;
  }
  .quotes__grid {
    display: none;
  }
  .quotes__hidden {
    display: block;
  }
  .quotes__hidden .quotes .swiper-slide-active {
    transform: scale(1.025) !important;
  }
  .quotes__hidden .quotes__author-image {
    max-width: 12.5rem;
    bottom: -4rem !important;
  }
  .quotes__hidden .quotes__grid-icon {
    width: 1.5rem;
    height: 1.5rem;
  }
  .quotes__hidden .quotes__grid-info p {
    font-size: var(--text-base);
  }
  .quotes__hidden .quotes__grid-author {
    font-size: var(--text-sm);
  }
  .quotes__hidden .quotes__grid-item {
    padding: 1.5rem 1.5rem 1.875rem 1.5rem;
    transform: translateY(0) !important;
    min-height: 18.125rem;
  }
}
